console.log(numbers)
console.log(fruits)
console.log(numbers[1]) // Ïù∏Îç±Ïä§ Ïù∏Îç±Ïã±

numbers.stay = 3
console.log(numbers)

// .length
//prototypeÏùÄ Î¶¨ÌÑ∞Îü¥Î°ú ÏÉùÏÑ±Ìïú Í≤ÉÏóê Î∞îÎ°ú ÏÇ¨Ïö©Ìï† Ïàò ÏûàÎã§.
console.log([1, 2].length)

