// .concat()
console.log(numbers.concat(fruits)) //Ìï©Ï≥êÏ£ºÏßÄÎßå ÏõêÎ≥∏ Î∞∞Ïó¥ Îç∞Ïù¥ÌÑ∞ÏóêÎäî ÏòÅÌñ•Ïù¥ ÏóÜÏùå
console.log(numbers)
console.log(fruits)

// .forEach()
//return Í∞íÏù¥ ÏóÜÎã§.

fruits.forEach(function (element, index, array) {
    console.log(element, index, array)
})

// .map()
//return Í∞íÏúºÎ°ú Î∞∞Ïó¥ÏùÑ ÎßåÎì§Ïñ¥ Ï§å

const a = fruits.forEach((fruit, index) => {
    console.log(`${fruit}-${index}`)
})
console.log(a)

const b = fruits.map((fruit, index) => ({
    id: index,
    name: fruit
}))
console.log(b)

// .filter()

const c = numbers.map(number => number < 3)
console.log(c)

const d = numbers.filter(number => number < 3)
console.log(d)

// .find() .findIndex()

const e = fruits.find(fruit => /^C/.test(fruit))
console.log(e)

const f = fruits.findIndex(fruit => /^C/.test(fruit))
console.log(f)

// .includes()

const g = numbers.includes(3)
console.log(g)

const h = fruits.includes('HEROPY')
console.log(h)

// .push() .unshift()
// ÏõêÎ≥∏ ÏàòÏ†ïÎê® Ï£ºÏùò!

numbers.push(5)
console.log(numbers)

numbers.unshift(0)
console.log(numbers)

// .reverse()
// ÏõêÎ≥∏ ÏàòÏ†ïÎê® Ï£ºÏùò!

numbers.reverse()
fruits.reverse()

console.log(numbers)
console.log(fruits)

// .splice()
// ÏõêÎ≥∏ ÏàòÏ†ïÎê® Ï£ºÏùò!

